<template>
    <ul id="changePassword">
        <li>
            <span class="leftCon">手机号码</span>
            <input type="text" class="formInput" placeholder="请输入手机号码"  v-model="userMsg.phone">
            <span class="getCode" @click="getCode" :class="countFlag == true ? 'disabled_btn' : ''">{{countDownMsg}}</span>
        </li>
        <li>
            <span class="leftCon les4">验证码</span>
            <input type="text" class="formInput" placeholder="请输入手机验证码"  v-model="userMsg.smsCode">
        </li>
        <li>
            <span class="leftCon les4">新密码</span>
            <input type="password" class="formInput" placeholder="请输入新密码,长度为8-14位" v-model="userMsg.password">
        </li>
        <li>
            <span class="leftCon">确认密码</span>
            <input type="password" class="formInput" placeholder="再次输入新密码" v-model="userMsg.repassword">
        </li>
				<globalBtn class="sureButton" :value="subminBtn.value" :status="subminBtn.status" @validateCode="confirm" :btnState="subminBtn.btnState"></globalBtn>
    </ul>
</template>

<script>
	import { apiUrl } from '@/common/js/api'
	import { ajaxPost } from '@/common/js/public'
	import { Toast, Notify, Dialog } from 'vant'
	import globalBtn from '@/components/globalBtn.vue'
	
    export default {
        name: 'forgetPassword',
				components: {
					globalBtn
				},
        data(){
			return {
				userMsg: {
					phone: '',
					smsCode: '',
					password: '',
					repassword: '',
				},
				countDownMsg: '获取验证码',
				countDefaultTime: 61,
				countTime: 61,
				countFlag: false,
				timer: '',
				temporaryVal: '',
				subminBtn: {
					value: '确认信息',
					status: 'normal register_btn',
					btnState: false
				},
			}
        },
		methods: {
			maxLength(val, name) {
				let max = 30;
				if (name == 'smsCode') {
					max = 6;
				}
				if (val.length > max) {
					val = val.substring(0, max);
					this.userMsg[name] = val;
					return val;
				}
				return val;
			},
			checkPhone(phone){ 
				if(!(/^1[34578]\d{9}$/.test(phone))){ 
					return false; 
				}
				return true;
			},
			notifyIt(msg, time = 1000) {
				this.$notify({
					message: msg,
					duration: time,
				})
			},
			setPayTime() {
				//倒计时
				clearTimeout(this.timer);
				this.countFlag = true;
				let setTime = () => {
					if(this.countTime == 0) {
						this.timeFlag = false;
						this.countTime = this.countDefaultTime;
					} else {
						this.countTime--;
						this.timer = setTimeout(() => {
							setTime();
						}, 1000)
					}
				}
				setTime();
			},
			confirm() {
				let confirmFlag = false;
				//非空验证
				for (var key in this.userMsg) {
					if (this.userMsg[key] == '') {
						console.log('信息不能为空');
						this.notifyIt('信息不能为空');
						confirmFlag = false;
						return;
					}
				}
				
				//手机号验证
				if (!this.checkPhone(this.userMsg.phone)) {
					console.log('手机号错误');
					this.notifyIt('手机号错误');
					confirmFlag = false;
					return false;
				}
				
				//密码验证
				if (this.userMsg.password > 14 || this.userMsg.password < 8) {
					this.notifyIt('密码长度为8-14位');
					console.log('密码长度不合规范');
					confirmFlag = false;
					return false;
				}
				
				if (this.userMsg.password !== this.userMsg.repassword) {
					console.log('密码不一致');
					this.notifyIt('密码不一致');
					confirmFlag = false;
					return false;
				}
				
				confirmFlag = true;
				this.subminBtn.btnState = true;
				console.log('修改！');
				var _this = this;
				/* ajaxPost(apiUrl.resetPassword, {
					"phone": this.userMsg.phone,
					"newPassword": this.userMsg.password,
					//"oldPassword": this.userMsg.rePassword,
					"sixCode": this.userMsg.smsCode,
				}, (data) => {
					data = data.data.body; */
					data = {
						resultCode: 1000
					}
					console.log(data);
					_this.subminBtn.btnState = false;
					/*
						1000 成功
						1001 参数为空
						1111 手机号未注册
						3001 验证码错误
						3002 验证码失效
					*/
					if (data.resultCode == 1000) {
						console.log('修改成功');
						_this.$dialog.alert({
						  title: '修改成功！',
						  message: '密码修改成功，请重新登录。'
						}).then(() => {
							_this.$router.push({name:'login'});
						});
					} else if (data.resultCode == 1001) {
						console.log('无效操作');
						_this.notifyIt('无效操作');
					} else if (data.resultCode == 3001) {
						console.log('验证码错误');
						_this.notifyIt('验证码错误');
					} else if (data.resultCode == 3002) {
						console.log('验证码失效');
						_this.notifyIt('验证码失效');
					} else if (data.resultCode == 1111) {
						console.log('手机号未注册');
						_this.notifyIt('手机号未注册');
					}
				/* }, () => {
					_this.subminBtn.btnState = false;
					_this.notifyIt('网络繁忙');
				}) */
			},
			getCode() {
				if (!this.countFlag) {
					if (!this.checkPhone(this.userMsg.phone)) {
						this.notifyIt('手机号错误');
						console.log('手机号错误');
					} else {
						//发请求
						var _this = this;
						ajaxPost(apiUrl.getSystemSMS, {
							"module": "resetPs",
							"phone": this.userMsg.phone,
						}, (data) => {
							//倒计时
							data = data.data.body;
							/* 
								1000 成功
								1001 参数为空
								1110 业务异常
								3004 发送太频繁
								1111 手机未注册
								3005 验证码次数已经达到上限
							*/
							if (data.resultCode == 1000) {
								console.log('发送验证码成功');
								_this.setPayTime();
							} else if (data.resultCode == 1001) {
								console.log('参数为空，验证码错误');
								_this.notifyIt('参数为空，验证码错误');
							} else if (data.resultCode == 1110) {
								console.log('网络繁忙');
								_this.notifyIt('网络繁忙');
							} else if (data.resultCode == 3004) {
								console.log('请不要频繁操作');
								_this.notifyIt('请不要频繁操作');
							} else if (data.resultCode == 1111) {
								console.log('手机号未注册');
								_this.notifyIt('手机号未注册');
							}else if (data.resultCode == 3005) {
								console.log('今天您的验证码次数已经达到上限');
								_this.notifyIt('今天您的验证码次数已经达到上限');
							}
						}, () => {
							_this.notifyIt('网络繁忙');
						})
					}
				}
			},
		},
		watch: {
			countTime(newVal, oldVal) {
				if (oldVal > 0) {
					this.countDownMsg = '重新获取('+ this.countTime +'s)';
				} else {
					this.countFlag = false;
					this.countDownMsg = '获取验证码';
				}
			},
			'userMsg.password'(newVal) {
				this.userMsg.password = this.maxLength(newVal, 'password');
			},
			'userMsg.repassword'(newVal) {
				this.userMsg.repassword = this.maxLength(newVal, 'repassword');
			},
			'userMsg.phone'(newVal) {
				this.userMsg.phone = this.maxLength(newVal, 'phone');
			},
			'userMsg.smsCode'(newVal) {
				this.userMsg.smsCode = this.maxLength(newVal, 'smsCode');
			}
		}
    }
</script>

<style scoped lang="less">
	@import '../common/css/global_pyt.less';
	
	#changePassword{
		width: 100%;
        box-sizing: border-box;
		li{
			box-sizing: border-box;
			width: 100%;
			height: .43*2rem;
            line-height: .43*2rem;
            padding-left: 0.15*2rem;
            padding-right: 0.15*2rem;
            border-bottom: 1px solid #F6F7F8;
            font-size: 0.14*2rem;
            background-size: 0.04*2rem 0.1*2rem;
            color: #272727;
			.leftCon{
				display: block;
				width: 0.76*2rem;
				float: left;
				overflow: hidden;
			}
			.formInput{
				float: left;
				width: 1.8*2rem;
				height: 0.3*2rem;
				line-height: 0.33*2rem;
				margin-top: 0.05*2rem;
				border: none;
			}
			.getCode{
				width: 0.84*2rem;
				height: 0.3*2rem;
				line-height: 0.3*2rem;
				box-sizing: border-box;
				border: 1px solid #3391E8;
				font-size: 0.12*2rem;
				display: block;
                position: absolute;
				text-align: center;
				color: #3391E8;
                top: 0.06*2rem;
                right: 0.15*2rem;
				/*margin-top: 0.06*2rem;*/
				border-radius: 2px;
			}
			.les4{
				letter-spacing: 0.07*2rem;
			}
			.disabled_btn {
				color: #B1AFAF;
				border-color: #B1AFAF; 	
			}
		}
		.sureButton{
			width: 3.2*2rem;
            height: 0.44*2rem;
            line-height: 0.44*2rem;
            text-align: center;
            background-color: #3391E8;
            color: #fff;
            margin: 1.64*2rem 0 0 0.27*2rem;
            border-radius: 4px;
            letter-spacing: 4px;
            font-size: 0.18*2rem;
            font-family: PingFangSC-Medium;
            box-shadow: 0px 4px 6px rgba(45,136,219,0.3);
		}
	}
</style>


